import axios from "axios";

const apiurl=process.env.apiurl;
var url = apiurl+"api/getList/";

class getList {
  static async get(data) {
    try {
      return await axios.get(url, {
        headers: {
          Authorization: data.verificationToken,
          User: data.userId
        }
      });
    } catch (e) {
      console.log(e);
    }
  }
}
export default getList;
