import axios from "axios";

const apiurl=process.env.apiurl;
var url = apiurl+"api/login/";

class loginService {
  static async getter(data) {
    try {
      const res = await axios.post(url, data);
      return res.data.data;
    } catch (err) {
      console.log(err);
    }
  }
}
export default loginService;
